body 
{
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.App
{
  width: 95vw;
}
ul
{
  width:40vw;
  list-style: none;
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}
nav a
{
  color: black;
  text-decoration: none;
  font-size: 1.2rem;
  font-weight: 700;
}
nav a:hover{
  background-color:black;
  color: white;
  padding: 0.5rem;
  transition: all 0.5s ease-in-out;
}
.nav
{
  border-bottom: 3px solid black;
  display:flex;
  justify-content: center;
} 
h1
{
  text-align: center;
}
p
{
  margin: 2% 30%;
  text-align:justify;
}
.article
{
  width:50vw;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin: 0 30%;
}
.article a
{
  text-decoration: none;
  color: black;
}
.article p, .article-other p
{
  margin: 0%;
}
.article-other,.comments,.comment-form
{
  width:50vw;
  margin: 2% 25%;
  font-size:100;
}
.article-other a{
  text-decoration: none;
  color: rgb(94, 82, 82);
}
.article-other a:hover
{
  color: black;
}
.comment h4,.comment p
{
  padding:0 0 1% 8%;
  margin:0;
}
.comment p
{
  border-bottom: 2px solid;
}
button
{
  background-color: black;
  color: white;
  padding:0.9%;
  border-radius:0.25rem;
  cursor: pointer;
}
.vote
{
  margin: 2% 30%;
  display: flex;
}
.vote p
{
  margin:0 0 0 3%;
}
.comment-form
{
  display: grid;
}
.comment-form input , .comment-form textarea
{
  margin: 2% 5%;
  display: inline-table;

}